import React from 'react';
import grey from '@material-ui/core/colors/grey';
import { makeStyles, Theme } from '@material-ui/core';

const useStyles = makeStyles<Theme, { size: number; color: string }>({
  '@keyframes rippleKeyframe': {
    from: {
      top: '50%',
      left: '50%',
      width: 0,
      height: 0,
      opacity: 1,
    },
    to: {
      top: 0,
      left: 0,
      width: '100%',
      height: '100%',
      opacity: 0,
    },
  },
  root: {
    textAlign: 'center',
  },
  text: {
    fontSize: '0.8rem',
    margin: 0,
    color: grey['800'],
  },
  wrapper: {
    width: props => `${props.size}px`,
    height: props => `${props.size}px`,
    position: 'relative',
    display: 'inline-block',
    verticalAlign: 'middle',
  },
  circle: {
    position: 'absolute',
    border: props => `${props.size / 20}px solid ${props.color}`,
    opacity: 1,
    borderRadius: '50%',
    animation: '$rippleKeyframe 1s cubic-bezier(0, 0.2, 0.8, 1) infinite',
    '&:nth-of-type(2)': {
      animationDelay: '-0.5s',
    },
  },
});

interface Props {
  size?: number;
  color?: string;
  text?: string;
}
const Loading = ({ size = 80, text = '', color = grey['500'] }: Props) => {
  const styles = useStyles({ size, color });
  return (
    <div className={styles.root}>
      <div className={styles.wrapper}>
        <div className={styles.circle} />
        <div className={styles.circle} />
      </div>
      {text && <p className={styles.text}>{text}</p>}
    </div>
  );
};

export default Loading;
